    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Caractersticas de la cartografa social

    p.mb-5.mt-5 Esta tcnica posee atributos que la convierten en una herramienta poderosa para el anlisis colectivo y el fortalecimiento comunitario:

