      .col-lg-8.order-lg-1
        p La cartografa productiva participativa es una herramienta metodolgica que permite identificar, analizar y representar colectivamente las actividades econmicas y productivas de una comunidad o territorio. Estos mapas son elaborados por los propios actores locales (campesinos, artesanos, pescadores, etc.), quienes poseen un conocimiento profundo de su entorno. Este tipo de cartografa es una forma especfica de cartografa social, pero con un enfoque claramente orientado a lo econmico y productivo. Su propsito es:
        .bg-color-9.tarjeta.p-4.mb-3(data-aos="fade-left")
          ul.lista-ul--color.mb-0
            li.mb-2.d-flex.bg-white.tarjeta.p-2
