            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Reconocer quin produce qu, cmo, dnde y para quin.
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Analizar oportunidades, limitaciones y relaciones econmicas.
    
    p.mb-5 Se le denomina participativa porque la comunidad es quien construye el mapa, no tcnicos externos. Esto permite valorar el conocimiento local y ponerlo al centro del anlisis territorial y productivo.

    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Usos de la cartografa productiva participativa

    p.mb-5.mt-5 Esta herramienta sirve para mltiples fines en procesos de investigacin o planificacin:

