    p.mb-5 La cartografa productiva participativa permite a las comunidades comprender su economa local, visibilizar su papel productivo, planificar de forma autnoma y sostenible, y fortalecer su empoderamiento dentro de procesos de desarrollo rural o territorial.

    .titulo-con-imagen.mb-5(data-aos="fade-right")
