        p(data-aos="fade-left") El territorio no es solo el lugar fsico donde una comunidad vive, trabaja y se organiza, sino tambin todo lo que ese espacio significa para las personas. Es decir, incluye los recursos naturales, los espacios construidos, los vnculos sociales, la historia, la cultura y las emociones asociadas al entorno. En este contexto, un mapa de territorio es una herramienta visual construida por la comunidad, en la que se representa su espacio, su vida cotidiana y su realidad desde una perspectiva propia. No se trata de un mapa tcnico, sino de un dibujo colectivo que expresa lo que la comunidad considera importante de su territorio.

    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Qu muestra un mapa de territorio?

    p.mb-5.mt-5 Este tipo de mapa representa diversos elementos clave:

