              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Lugares importantes para la vida, la cultura, las costumbres y la historia local.
              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Problemas o cambios en el territorio, sean positivos o negativos (como deforestacin, contaminacin, prdida de tierras, construccin de carreteras, entre otros).
              li.mb-2.d-flex.bg-white.tarjeta.p-2
                i.fas.fa-leaf.bg-color-7
                p.mb-0 Zonas de recursos naturales, de trabajo, de peligro o conflicto.

    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Para qu sirve un mapa de territorio?

    p.mb-5.mt-5 Un mapa de este tipo es til para la investigacin y la organizacin comunitaria, por varias razones:

