            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Permite reflexionar sobre el entorno y cmo se utiliza.
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Facilita la identificacin de problemas o necesidades del territorio.
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Ayuda a planificar acciones futuras, como el cuidado del agua, la produccin agrcola o la defensa del territorio.
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Visibiliza el conocimiento del territorio desde las voces de quienes lo habitan.
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Fomenta el dilogo entre la comunidad y otras instituciones (como municipios, ONGs o el Estado).
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Permite valorar el saber local y promover la participacin de distintos actores (nios, mujeres, personas mayores).
            li.mb-2.d-flex.bg-white.tarjeta.p-2
              i.fas.fa-leaf.bg-color-7
              p.mb-0 Deja un material til para la comunidad, incluso despus de concluida la investigacin.
        p El mapa de territorio permite que las comunidades se visualicen y se comprendan a s mismas desde su propia mirada. No solo representa el espacio fsico, sino tambin las relaciones, la cultura, la memoria y las proyecciones hacia el futuro.
    
    p.mb-5 A continuacin, se presenta un video que aborda las diferencias entre la cartografa social, la cartografa productiva participativa y el mapa de territorio, destacando su utilidad como herramientas de investigacin y planificacin comunitaria en contextos rurales.

