        p(data-aos="fade-left") El grupo focal es una tcnica cualitativa que consiste en reunir a un grupo pequeo de personas (generalmente entre 6 y 12 participantes) para conversar de manera guiada y abierta sobre un tema especfico relacionado con el objeto de estudio.
    
