    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Aplicacin del FODA en comunidades campesinas

    p.mb-5.mt-5 En contextos rurales, el mtodo FODA se adapta a procesos participativos y colaborativos, donde se valora el conocimiento local. El enfoque es colectivo, inclusivo y basado en el dilogo.

    .row.mb-5
      .col-lg-3.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema2/56.png", data-aos="zoom-in")
      .col-lg-9
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-color-4")
          .div(titulo="Preparacin y sensibilizacin")
            p Se inicia con una explicacin clara y sencilla sobre qu es el FODA y cul es su utilidad. Es fundamental crear un ambiente de confianza y respeto que fomente la participacin abierta de todos los asistentes.
    
          .div(titulo="Construccin de la matriz comunitaria")
            p Se trabaja en grupo o en asamblea, utilizando materiales como papelgrafos, pizarras o tcnicas participativas. Se elabora una matriz FODA comunitaria con los siguientes cuadrantes y preguntas: Fortalezas (internas): Qu hacemos bien? / Qu recursos tenemos? Oportunidades (externas): Qu cosas del entorno pueden ayudarnos? / Qu apoyos externos existen? Debilidades (internas): Qu nos est limitando? / Qu nos falta o falla? Amenazas (externas): Qu cosas externas nos perjudican? / Qu cambios podran afectarnos negativamente?
    
          .div(titulo="Participacin comunitaria")
            p Durante el taller, las personas dialogan y llenan colectivamente cada cuadrante de la matriz, compartiendo experiencias, propuestas y puntos de vista para llegar a consensos.
          .div(titulo="Anlisis colectivo")
            p Una vez completada la matriz, se examinan las relaciones entre los elementos, como: Cmo puede una fortaleza aprovechar una oportunidad? / Cmo una debilidad puede agravarse con una amenaza? Adems, se priorizan los aspectos ms urgentes o estratgicos.
    
          .div(titulo="Planificacin")
            p A partir del anlisis FODA, se construyen acciones concretas como proyectos comunitarios, alianzas, capacitaciones, compras colectivas o mejoras organizativas.

    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Ventajas del FODA en la investigacin participativa

    p.mb-5.mt-5 Este mtodo ofrece mltiples beneficios cuando se aplica con enfoque participativo:

