    h3.text-white.bg-color-21.d-inline.p-2.pst-3 Qu es y para qu sirve?

    p.mb-5.mt-5 Este instrumento sirve para recordar, reflexionar y analizar todo lo que ocurre a lo largo del proceso de investigacin. No se trata solo de registrar datos, sino tambin de plasmar experiencias, emociones, dificultades, aprendizajes, ideas, dudas y conexiones que se van descubriendo en el camino. En otras palabras, el diario de campo funciona como un cuaderno personal de trabajo, donde se puede anotar:

